body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
}

h1,
h2,
h3,
h4,
h5,
h6,
.header__title,
.menu__body ul li a {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  line-height: 1.2;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 1.625rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

strong {
  font-weight: 700;
}

a,
p {
  text-decoration: none;
  font-size: 1rem;
}

ul {
  margin: 0;
}

.button {
  display: inline-block;
  font-size: 16px;
  font-weight: 600;
  padding: 0.875rem 2rem;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  border: 1px solid transparent;
  border-radius: 50px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  min-width: 6.25rem;
}
.button:hover {
  filter: brightness(0.9);
}
.button:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.5);
}
.button:disabled, .button.disabled {
  cursor: not-allowed;
  opacity: 0.6;
}

.button--primary {
  background-color: #dd8603;
  color: #ffffff;
  border-color: #dd8603;
}
.button--secondary {
  background-color: transparent;
  color: #ffffff;
  border-color: #dd8603;
}
.button--outline {
  background-color: transparent;
  color: #dd8603;
  border-color: #dd8603;
}
.button--outline:hover {
  background-color: #dd8603;
  color: #ffffff;
}

ul.social-links {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;
  text-align: center;
  padding: 0;
}

.header-banner {
  position: relative;
  width: 100%;
  height: 800px;
  overflow: hidden;
  z-index: 9;
  margin-top: -118px;
}
.header-banner__image::before {
  content: ""; /* Required to use the pseudo-element */
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.5); /* Semi-transparent black overlay */
  z-index: 1;
  opacity: 0; /* Start as invisible */
  animation: fadeIn 1.5s ease 1.4s forwards; /* 0.5s fade-in after a 3s delay */
}
.header-banner__image img {
  width: 100%;
  height: 800px;
  object-fit: cover;
  object-position: top;
  animation: slideIn 1.5s ease-in-out forwards;
}
.header-banner__content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  opacity: 0;
  animation: fadeIn 1.5s ease-in-out forwards;
  animation-delay: 1s;
  max-width: 800px;
  width: 100%;
  z-index: 2;
}
.header-banner__content h1 {
  font-size: 3.25rem;
  font-weight: 700;
  color: #ffffff;
}
@media (min-width: 768px) {
  .header-banner__content h1 {
    font-size: 5.75rem;
  }
}
.header-banner__content p {
  font-size: 1.75rem;
  margin: 0;
  color: #dd8603;
  letter-spacing: 0.125rem;
}
@media (min-width: 768px) {
  .header-banner__content p {
    letter-spacing: 0.375rem;
    font-size: 2rem;
  }
}
.header-banner__cta {
  position: absolute;
  bottom: -100px; /* Initially hidden */
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  transition: all 1.5s ease-in-out;
  z-index: 3;
  font-size: 16px;
  padding: 24px 32px;
}
@media (min-width: 768px) {
  .header-banner__cta {
    padding: 24px 64px;
  }
}

.header {
  top: 0;
  left: 0;
  width: 100%;
  position: relative;
  z-index: 10;
  background-color: rgba(25, 31, 40, 0.7);
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}
.header__container {
  padding: 0 30px;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 70px;
}
.header__logo a {
  color: #ffffff;
  font-size: 1.875rem;
  display: flex;
  gap: 1rem;
}
.header__menu-cta.d-mobile-only {
  position: absolute;
  bottom: 4rem;
  left: 0;
  right: 0;
}

.menu__icon {
  display: none;
}

.menu__arrow {
  display: none;
}

.menu__body .menu {
  display: grid;
  gap: 3rem;
  padding: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .menu__body .menu {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.menu__body .menu li {
  list-style: none;
}
.menu__body .menu li a {
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.3s ease-in-out;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .menu__body .menu li a {
    font-size: 1rem;
  }
}
.menu__body .menu li a:hover {
  color: #dd8603;
}
.menu__body .menu li.current-menu-item a {
  color: #dd8603;
}

/*-------------------------*/
body._pc .menu__list > li:hover .menu__sub-list {
  opacity: 1;
  visibility: visible;
  transform: translate(0, 0);
  pointer-events: all;
}

body._touch .menu__list > li {
  display: flex;
  align-items: center;
}

body._touch .menu__link {
  flex: 1 1 auto;
}

body._touch .menu__arrow {
  display: block;
  width: 0;
  height: 0;
  margin-left: 5px;
  transition: transform 0.3s ease 0s;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 10px solid #fff;
}

body._touch .menu__list > li._active .menu__sub-list {
  opacity: 1;
  visibility: visible;
  transform: translate(0, 0);
  pointer-events: all;
}

body._touch .menu__list > li._active .menu__arrow {
  transform: rotate(180deg);
}

/*-------------------------*/
@media (min-width: 767px) {
  .menu__list {
    display: flex;
    align-items: center;
  }
  .menu__list > li {
    /* padding from link to sub links area to avoid breaking hover on mouse move */
    padding: 10px 0;
  }
  .menu__sub-list {
    transform: translate(0, 10px);
    opacity: 0;
    visibility: hidden;
    /* to avoid mistaken hover */
    pointer-events: none;
    transition: all 0.3s ease 0s;
  }
}
@media (max-width: 767px) {
  .header {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
  }
  .menu__icon {
    z-index: 5;
    display: block;
    position: relative;
    width: 30px;
    height: 18px;
    cursor: pointer;
  }
  .menu__icon span,
  .menu__icon::before,
  .menu__icon::after {
    left: 0;
    position: absolute;
    height: 10%;
    width: 100%;
    transition: all 0.3s ease 0s;
    background-color: #fff;
  }
  .menu__icon::before,
  .menu__icon::after {
    content: "";
  }
  .menu__icon::before {
    top: 0;
  }
  .menu__icon::after {
    bottom: 0;
  }
  .menu__icon span {
    top: 50%;
    transform: scale(1) translate(0, -50%);
  }
  .menu__icon._active span {
    transform: scale(0) translate(0, -50%);
  }
  .menu__icon._active::before {
    top: 50%;
    transform: rotate(-45deg) translate(0, -50%);
  }
  .menu__icon._active::after {
    bottom: 50%;
    transform: rotate(45deg) translate(0, 50%);
  }
  .menu__body {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.9);
    padding: 200px 20px 20px;
    transition: left 0.3s ease 0s;
    /* for scroll on menu */
    overflow: auto;
  }
  .menu__body._active {
    /* use left and not transform/translate because it scroll the .menu__body::before when u scroll menu */
    left: 0;
  }
  .menu__list > li {
    flex-wrap: wrap;
    margin-bottom: 30px;
  }
  .menu__list > li:last-child {
    margin-bottom: 0;
  }
  .menu__list > li._active .menu__sub-list {
    display: block;
  }
  .menu__link {
    font-size: 24px;
  }
  .menu__sub-list {
    position: relative;
    background-color: #fff;
    flex: 1 1 100%;
    margin-top: 20px;
    display: none;
  }
  .menu__sub-link {
    font-size: 20px;
    color: #000;
  }
}
.d-mobile-only {
  display: block !important;
}
@media (min-width: 768px) {
  .d-mobile-only {
    display: none !important;
  }
}

.d-desktop-only {
  display: none !important;
}
@media (min-width: 768px) {
  .d-desktop-only {
    display: block !important;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes slideIn {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}